import Vue from 'vue'
import Vuex from 'vuex'
// import app from './modules/app'
// const { app } = require('./../store/modules/app.js')

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    account: '',
    appId: ''
  },
  mutations: {
    GET_ACCOUNT: (state, account) => {
      sessionStorage.setItem('username', account)
      state.account = account
    },
    GET_APPID: (state, appid) => {
      sessionStorage.setItem('appId', appid)
      state.appId = appid
    }
  },
  actions: {
    getAccount ({ commit }, account) {
      commit('GET_ACCOUNT', account)
    },
    getappid ({ commit }, appid) {
      commit('GET_APPID', appid)
    }
  }
})
